<style lang="postcss" module>
.header {
  height: 60px;
  width: 100%;
  background: #000000;
  .login-reg {
    float: right;
    margin-top: 19px;
    color: #9A9A9A;
    .button {
      border: none;
      background: transparent;
      color: #9A9A9A;
      font-size: 16px;
      cursor: pointer;
    }
  }
}
.header-inner {
  width: 1160px;
  height: 100%;
  margin: 0 auto;
  position: relative;
}
.logo {
  position: absolute;
  top: 12px;
  width: 91px;
  height: 41px;
  background-image: url('../../assets/music/logo.png');
  background-size: contain;
  background-repeat: no-repeat;

  & > a {
    display: block;
    width: 100%;
    height: 100%;
  }
}
.body{
  width: 1240px;
  margin: 0 auto;
  margin-bottom: 69px;
  .banner {
    width: 1240px;
    margin: 0 auto;
    img {
      width: 1240px;
      height: 330px;
      margin: 0 auto;
    }
  }
  .content {
    display: inline-block;
    width: 885px;
    .playsmall {
      width: 50px;
      height: 50px;
      position: relative;
    }
    .playsmall::after {
      display: none;
      content: ' ';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: url('../../assets/music/play.png');
      background-repeat: no-repeat;
      background-size: 20px;
      background-position: center center;
    }
    .playsmall:hover:after {
      display: block;
      cursor: pointer;
    }
    h1 {
      margin: 23px 0 22px 35px;
      font-size: 24px;
    }
    .title {
      width: 834px;
      height: 54px;
      margin-left: 32px;
      display: flex;
      align-items: center;
      justify-content: space-around;
      color: #A0A0A0;
    }
    &Musics {
      width: 834px;
      height: 80px;
      line-height: 80px;
      margin-left: 26px;
      border-top: 1px solid #F2F2F2;
      &:hover {
        background: #F2F2F2;
      }
      &:last-child {
        border-bottom: 1px solid #F2F2F2;
      }
      strong {
        display: inline-block;
        width: 15px;
        margin-left: 12px;
      }
      img {
        width: 50px;
        height: 50px;
        display: inline-block;
        vertical-align: middle;
        margin: 0 5px 0 5px;
      }
      span{
        display: inline-block;
        &:nth-of-type(1) {
          width: 200px;
        }
        &:nth-of-type(2) {
          width: 208px;
        }
        &:nth-of-type(3) {
          width: 190px;
        }
      }
    }
    .mv-list {
      .mv {
        display: inline-block;
        &:first-child {
          margin-left: 26px;
        }
        &:not(:first-child) {
          margin-left: 19px;
        }
        .link {
          display: block;
          position: relative;
          img {
            width: 150px;
            display: block;
          }
        }
        .link::after {
          display: none;
          content: ' ';
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-color: rgba(0,0,0,0.5);
          background-image: url('../../assets/music/play.png');
          background-repeat: no-repeat;
          background-size: 40px;
          background-position: center center;
        }
        .link:hover::after {
          display: block;
        }
        span {
          margin-top: 14px;
          font-size: 15px;
          color: #1A1A1A;
          display: block;
        }
      }
    }
  }
  .side {
    display: inline-block;
    vertical-align: top;
    margin-top: 20px;
    img {
      width: 333px;
      height: 333px;
    }
  }
}
.footer {
  background: #222222;
  height: 120px;
  width: 100%;
  overflow: hidden;
  .copy-right {
    height: 80px;
    font-size: 15px;
    color: #9A9A9A;
    margin: 20px auto;
    text-align: center;
    .hr {
      width: 1180px;
      height: 1px;
      margin: 0 auto;
      margin-top: 23px;
      margin-bottom: 15px;
      background: #2D2D2D;
    }
    ul {
      width: 165px;
      margin: 0 auto;
      overflow: hidden;
      li {
        float: left;
        a:hover {
          color: #FABE00;
        }
      }
      li:nth-child(2) {
        margin: 0 18px;
      }
    }
    p {
      font-size: 13px;
      margin-top: 16px;
      line-height: 18px;
      color: #595959;
      span {
        margin: 0 12px;
      }
    }
    p:last-child {
      margin-top: 5px;
      line-height: 18px;
    }
  }
}
.login {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  background: rgba(0, 0, 0, .3);
  .block {
    box-sizing: border-box;
    width: 400px;
    top: 50%;
    left: 50%;
    position: relative;
    transform: translate(-50%, -50%);
    background: #FFF;
    border-radius: 5px;
    padding: 19px 35px 35px;
    .close {
      cursor: pointer;
      position: absolute;
      top: 7px;
      right: 8px;
    }
    h2 {
      text-align: center;
    }
    .hr {
      width: 100%;
      margin-top: 19px;
      height: 1px;
      background: #EAEAEA;
    }
    .input {
      box-sizing: border-box;
      width: 100%;
      border: 1px solid #DDD;
      border-radius: 3px;
      background: #F4F4F4;
      height: 40px;
      padding-left: 15px;
      margin-top: 20px;
      &::placeholder {
        color: #C8C8C8;
        font-size: 14px;
        padding: 10px 0;
      }
    }
    .verticalCode {
      width: 202px;
    }
    .verticalCodeWrap {
      display: flex;
    }
    button.sendMsg {
      box-sizing: border-box;
      width: 108px;
      height: 40px;
      border: 1px solid #DDD;
      border-radius: 3px;
      background: #F4F4F4;
      margin-top: 20px;
      margin-left: 20px;
      color: #C8C8C8;
    }
    button.submit {
      background: #F9BE00;
      color: #FFF;
      font-size: 16px;
      border: none;
      width: 100%;
      border-radius: 5px;
      height: 42px;
      cursor: pointer;
    }
    .forget {
      display: block;
      float: right;
      margin-top: 10px;
      margin-bottom: 15px;
    }
    .service {
      text-align: center;
      margin-top: 10px;
      margin-bottom: 15px;
    }
  }
  .cantplay {
    text-align: center;
    p {
      color: #9A9A9A;
      font-size: 17px;
      margin: 10px 0 28px;
    }
  }
}
</style>



